___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --bg-color-darkred: #5a0f19;
  --bg-color-darkorange: #8a3119;
  --bg-color-brown: #945737;
  --bg-color-bege: #ebdacc;
  --bs-emphasis-color-rgb: #945737;
}

.test {
  width: 100%;
  height: 100vh;
  background-color: var(--bg-color-bege);
}

.navbar {
  background-color: var(--bg-color-darkred);
  position: sticky;
  top: 0;
  z-index: 1;

  & .menu {
    font-size: 30px;
  }
}

.container {
  background-color: var(--bg-color-bege);
}

.col {
  & .card {
    padding: 2px;
    background-color: var(--bg-color-brown);
    color: var(--bg-color-bege);
    font-size: 1.2em;
  }
}

.containerForm {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

@media (max-width: 576px) {
  .card {
    width: 90%;
    height: 20rem;
  }
}

@media (min-width: 576px) {
  .card {
    width: 65%;
    height: 20rem;
  }
}

@media (min-width: 768px) {
  .card {
    width: 85%;
    height: 20rem;
  }
